"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Search, MoreVertical } from "lucide-react"

const mockCustomers = [
  { id: 1, name: "Amit Kumar", upiId: "amit@upi", stamps: 12, visits: 15, lastVisit: "2 days ago" },
  { id: 2, name: "Priya Singh", upiId: "priya@upi", stamps: 8, visits: 10, lastVisit: "1 day ago" },
  { id: 3, name: "Vikram Patel", upiId: "vikram@upi", stamps: 5, visits: 6, lastVisit: "3 days ago" },
  { id: 4, name: "Neha Sharma", upiId: "neha@upi", stamps: 20, visits: 25, lastVisit: "Today" },
]

export default function CustomersList() {
  const [search, setSearch] = useState("")

  const filtered = mockCustomers.filter(
    (c) =>
      c.name.toLowerCase().includes(search.toLowerCase()) ||
      c.upiId.toLowerCase().includes(search.toLowerCase())
  )

  return (
    <div className="space-y-2"> {/* ðŸ‘ˆ very tight spacing */}
      
      {/* Search */}
      <div className="relative max-w-xs">
        <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
        <Input
          placeholder="Search customers"
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="h-9 pl-8 text-sm"
        />
      </div>

      {/* Table */}
      <Card className="border">
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead className="border-b bg-muted/40">
              <tr className="text-muted-foreground">
                <th className="px-4 py-2 text-left font-medium">Customer</th>
                <th className="px-4 py-2 text-left font-medium">UPI</th>
                <th className="px-4 py-2 text-center font-medium">Stamps</th>
                <th className="px-4 py-2 text-center font-medium">Visits</th>
                <th className="px-4 py-2 text-left font-medium">Last</th>
                <th className="px-3 py-2"></th>
              </tr>
            </thead>

            <tbody>
              {filtered.map((c) => (
                <tr
                  key={c.id}
                  className="border-b last:border-0 hover:bg-muted/30 transition"
                >
                  {/* Name */}
                  <td className="px-4 py-2">
                    <div className="flex items-center gap-2">
                      <div className="h-7 w-7 rounded-full bg-primary/10 text-primary flex items-center justify-center text-xs font-semibold">
                        {c.name[0]}
                      </div>
                      <span className="font-medium">{c.name}</span>
                    </div>
                  </td>

                  {/* UPI */}
                  <td className="px-4 py-2 text-muted-foreground">
                    {c.upiId}
                  </td>

                  {/* Stamps */}
                  <td className="px-4 py-2 text-center">
                    <span className="inline-flex h-6 min-w-[24px] items-center justify-center rounded-full bg-primary text-white text-xs font-medium">
                      {c.stamps}
                    </span>
                  </td>

                  {/* Visits */}
                  <td className="px-4 py-2 text-center">
                    {c.visits}
                  </td>

                  {/* Last Visit */}
                  <td className="px-4 py-2 text-muted-foreground">
                    {c.lastVisit}
                  </td>

                  {/* Actions */}
                  <td className="px-3 py-2 text-right">
                    <Button variant="ghost" size="icon" className="h-8 w-8">
                      <MoreVertical size={16} />
                    </Button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </Card>

      {filtered.length === 0 && (
        <div className="py-6 text-center text-sm text-muted-foreground">
          No customers found
        </div>
      )}
    </div>
  )
}
